import React from 'react';
import styled from 'styled-components';

// import PushUpsContext from './state/PushUpsContextState';
import PushUpsContext from '../../state/PushUpsContextState';
import { postJsonDataUrlAfterAPIpushups } from '../../../../functions/postJsonData'



const SPlanLink = styled.div`
/* margin:5px; */
background-color:#56cd56;
padding:10px;
/* display:flex; */
/* justify-content: space-between; */
/* flex-wrap:wrap; */
`
const SButtonWrapper = styled.div`
display:flex;
/* flex-flow:row wrap; */
div{
  /* flex:1 1 auto; */
/* display:flex; */
/* justify-content:space-around; */
}
button{
  margin:10px;
  border-radius:10px;
  /* width:50%; */
  };
`

const PublicPlanToList = ({ plan }) => {
  let { user, updateUser } = React.useContext(PushUpsContext);


  const publicPlanButtonClickAction = async (publicPlanID, action) => {

    console.log("button action, plan numberFrom0: ", publicPlanID, action);

    if (action == 'view') {
      console.log("view");
      return;
      // history.push({
      //   pathname: `/pushups/trainingPlans/view/${positionFrom0}`,
      //   // state: {
      //   //   personalPlanPositionFrom0: positionFrom0,
      //   //   plan: user.pushupPlans.personal[positionFrom0]
      //   // }
      // });


    }

    //// commit, duplicate, delete actions - on server side:
    let objForServer = { publicPlanID, action }
    try {
      let updatedUser = await postJsonDataUrlAfterAPIpushups('/interactWithPublicPlan', objForServer);
      updateUser({ ...updatedUser });
    } catch (err) { console.log(err); };
  }

  return (
    <div style={{ backgroundColor: 'lightBlue', margin: '3%', padding: '3%' }}>
      {/* <p>Name: {plan.name}</p> */}

      {/* <SPlanLink key={plan._id}> */}
      <h3>{plan.name}</h3>
      <p>Description: {plan.description}</p>

      <SButtonWrapper>
        {/* <div> */}
        <button onClick={() => publicPlanButtonClickAction(plan._id, 'commit')}>Commit</button>
        {/* </div> */}
        {/* <div> */}
        <button onClick={() => publicPlanButtonClickAction(plan._id, 'view')}>View (not working yet)</button>
        {/* </div> */}

      </SButtonWrapper>
      <p>Unfinished: {plan.executions.summary.completed}, length: {plan.executions.completed.length}</p>
      <p>Finished: {plan.executions.summary.uncompleted}, length: {plan.executions.uncompleted.length}</p>

      {/* </SPlanLink> */}

    </div>
  );
};

export default PublicPlanToList;